.item {
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  list-style: none;
  height: 45px;
  border-bottom: 1px solid #dcdcdf;
  box-shadow: 0px 1px 0px rgba(255, 255, 255, 0.6);
}

.box {
  display: flex;
  gap: 16px;
}

.block {
  width: 24px;
  height: 24px;
}

.category {
  font-size: 16px;
  line-height: 1.5;
}
